.inner {
    width: 100%;
    max-width: 1200px;
    padding: 0 32px;
    margin: 0 auto;
}

@media (max-width:768px) {
    .inner {
        padding: 0 16px;
    }
}

/* 모달 백그라운드 */
.modal-background {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.4;
    background-color: #000;
    z-index: 199;
}

/* 말줄임상자 */
.ellipsis-1 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.ellipsis-2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.ellipsis-3 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}

/* Text Decoration */
.line-through {
    text-decoration: line-through;
}

/* Show & Hide */
@media (max-width:768px) {
    .hide_768 {
        display: none !important;
    }
}