.header {
  position: relative;
  height: 64px;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.header-pc,
.header-mobile {
  height: 100%;
}

.header-mobile {
  display: none;
}

.header .inner {
  display: flex;
  max-width: 1440px;
  height: 100%;
}

@media(max-width:1024px) {
  .header {
    height: 50px;
  }

  .header-pc {
    display: none;
  }

  .header-mobile {
    display: block;
  }
}

/* 로고 */
.header__logo {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  height: 100%;
}

.header__logo h1 {
  display: flex;
  align-items: center;
  height: 100%;
}

.header__logo-link {
  display: inline-flex;
  align-items: center;
  width: 110px;
  height: 100%;
}

.header__logo-img {
  max-width: 100%;
  width: 100%;
}

/* 메뉴 */

.header__menu-wrap {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-left: 10px;
}

.header__menu {
  display: flex;
  align-items: center;
  position: relative;
  height: 100%;
  flex-shrink: 0;
}

.header__menu-list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.header__menu-list>li {
  display: flex;
  align-items: center;
  height: 100%;
}

.header__menu-list>li>a {
  padding: 6px 10px;
  font-weight: 700;
  font-size: 15px;
  color: #4a4a4a;
}

/* 서브메뉴 */
.header__menu-list .has-submenu.depth1 {
  position: relative;
  z-index: 100;
}

.has-submenu.depth1.hover::before {
  content: '';
  position: absolute;
  bottom: 4px;
  left: 50%;
  width: 0px;
  height: 0px;
  border-width: 0 12px 12px;
  border-style: solid;
  border-color: transparent transparent #ececec;
  transform: translateX(-50%);
  z-index: 21;
}

.has-submenu.depth1.hover::after {
  content: '';
  position: absolute;
  bottom: 4px;
  left: 50%;
  width: 0px;
  height: 0px;
  border-width: 0 10px 10px;
  border-style: solid;
  border-color: transparent transparent #fff;
  transform: translateX(-50%);
  z-index: 21;
}

.header__menu-submenu {
  display: none;
  position: absolute;
  top: calc(100% - 4px);
  height: fit-content;
  padding: 8px 0 12px;
  background-color: #fff;
  box-shadow: 8px 5px 8px 1px rgba(0, 10, 18, 0.1), 0 0 0 1px rgba(0, 10, 18, 0.1);
}

.lecture .header__menu-submenu {
  width: 180px;
  left: 0;
}

.community .header__menu-submenu {
  width: 120px;
  right: 0;
}

.has-submenu.hover>.header__menu-submenu {
  display: block;
}

.submenu-item {
  width: 100%;
}

.submenu-link {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  font-size: 13px;
}

.submenu-link.has-icon {
  display: flex;
  align-items: center;
}

.submenu-link.has-link {
  justify-content: space-between;
}

.submenu-icon {
  font-size: 15px;
}

.submenu-text .submenu-icon {
  margin-right: 8px;
}

.submenu-text {
  display: flex;
  align-items: center;
  font-size: 13px;
}

.header__menu-submenu .education .education__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__menu-submenu .education .education__text {
  display: flex;
  align-items: center;
  font-size: 14px;
}

.header__menu-submenu .education .education__text .education__icon {
  margin-right: 8px;
}

.header__menu-submenu .education .education__icon {
  font-size: 15px;
}

.header__menu-submenu.depth2,
.header__menu-submenu.depth3 {
  top: 0;
  left: 100%;
  height: 100%;
}

.menu-hr {
  display: block;
  width: 160px;
  height: 1px;
  margin: 12px auto;
  border: none;
  background: #e9ecef;
}

/* 검색창 */
.header__search {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 8px;
}

.header__search-input {
  width: 100%;
  padding: 6px 35px 6px 10px;
  font-size: 15px;
  border: 1px solid #ced4da;
  border-radius: 8px;
  background-color: #f6f6f6;
}

.header__search-input:focus {
  box-shadow: 0 0 0 0.125em rgba(29, 192, 120, 0.25);
  outline: 0.04rem auto #1dc078;
}

.header__search-input::placeholder {
  font-size: 13px;
}

.header__search-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 15px;
  padding: 0;

  border: 0;
  background-color: transparent;
}

.header__search-button span {
  color: #4a4a4a;
}

/* 로그인 */
.header__login {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  height: 100%;
  margin-left: 12px;
}

.header__login-before,
.header__login-after {
  display: flex;
  align-items: center;
  gap: 0 8px;
  height: 100%;
}

.header__login-before-item .header__login-before-item-button {
  padding: 6px 8px;
  color: #363636;
  border: 1px solid #dbdbdb;
  border-radius: 4px;
  background-color: transparent;
}

.header__login-before-item .header__login-before-item-button.bg-red {
  color: #fff;
  background-color: #ff7867;
}

.header__login-after-item {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
}

.header__login-after-item:not(:last-child) {
  padding: 0 4px;
}

.header__login-after-item:last-child {
  /* padding-left:4px; */
}

.header__login-after-item .header__login-item-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 30px;
  height: 100%;
  color: #4a4a4a;
  border: 0;
  background-color: transparent;
  transition: color 0.2s;
}

.header__login-item-btn:hover {
  color: #00c471;
}

.cart-badge-wrap {
  position: absolute;
  top: 12px;
  right: -2px;
}

.red-dot-wrap {
  position: absolute;
  top: 18px;
  right: -2px;
}

.header__login-after-item .btn-dashboard {
  max-width: none;
  height: auto;
  padding: 6px 12px;
  font-weight: 600;
  font-size: 16px;
  color: #212529;
  border: 1px solid #ced4da;
  border-radius: 8px;
  background-color: #fff;
  transition: background-color 0.2s;
}

.header__login-after-item .btn-dashboard:hover {
  background-color: #f8f9fa;
}

.header__login-after-item .btn-profile .profile-img-wrap {
  max-width: 26px;
  border: 1px solid #e5e5e5;
  border-radius: 50%;
  overflow: hidden;
  width: 100%;
}

.header__login-after-item .btn-profile img {
  max-width: 100%;
  width: 100%;
}

/* 모달 */
.header__login-after-item .modal {
  position: absolute;
  top: 100%;
  right: 0;
  z-index: 10;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.shopping-cart-modal {
  width: 440px;
  max-height: 420px;
  padding: 20px;
  border-radius: 8px;
}

.shopping-cart-modal__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  padding-bottom: 16px;
}

.shopping-cart-modal__title {
  font-size: 16px;
  font-weight: 700;
  color: #1b1c1d;
}

.shopping-cart-modal__title span {
  font-weight: 500;
  color: #00c471;
}

.shopping-cart-modal__price {
  font-size: 14px;
  color: #abb0b5;
}

.shopping-cart-modal__price span {
  font-size: 16px;
  font-weight: 700;
  color: #1b1c1d;
}

.shopping-cart-modal__list {
  display: flex;
  flex-direction: column;
  gap: 16px 0;
  align-items: center;
  max-height: 300px;
  padding: 16px 0;
  border-top: 1px solid #e9ebee;
  overflow-y: auto;
  scrollbar-width: thin;
}

.shopping-cart-modal__list-item {
  width: 100%;
  height: 60px;
}

.shopping-cart-modal__list-link {
  display: flex;
  gap: 0 16px;
}

.shopping-cart-modal__list-thumb {}

.shopping-cart-modal__list-thumb figure {
  width: 80px;
  height: 60px;
  border: 1px solid #f1f3f5;
  border-radius: 4px;
  overflow: hidden;
}

.shopping-cart-modal__list-thumb img {
  width: 100%;
  height: 100%;
}

.shopping-cart-modal__list-info {
  width: 100%;
}

.shopping-cart-modal__list-title {
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 13px;
  color: #3e4042;
  line-height: 1.25;
}

.shopping-cart-modal__list-price {
  font-weight: 400;
  font-size: 15px;
  color: #abb0b5;
}

.shopping-cart-modal__list-price .discount {
  text-decoration: line-through;
}

.shopping-cart-modal__list-price .current-price {
  font-weight: 700;
  font-size: 15px;
  color: #1b1c1d;
}

.shopping-cart-modal__show-all-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  background-color: #00c471;
  border: 1px solid #00c471;
  border-radius: 4px;
}

.profile-modal {
  right: -10px !important;
  width: 320px;
  border-radius: 3px;
}

.profile-modal__top {
  position: relative;
  padding: 0 16px;
  margin: 16px 0;
}

.profile-modal__user-info {
  display: flex;
  margin-bottom: 16px;
}

.profile-modal__user-thumb {
  display: flex;
  position: relative;
  width: 46px;
  height: 46px;
  margin-right: 8px;
  overflow: hidden;
  border-radius: 50%;
}

.profile-modal__user-thumb span {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 18px;
  font-size: 11px;
  color: #fff;
  background-color: rgba(33, 37, 41, 0.64);
}

.profile-modal__user-name {
  display: flex;
  align-items: center;
}

.profile-modal__user-name:hover .name {
  text-decoration: underline;
}

.profile-modal__user-name .name {
  font-weight: 900;
  font-size: 15px;
}

.profile-modal__user-name .icon {
  flex-shrink: 0;
  font-size: 20px;
}

.profile-modal__user-desc {
  margin-top: 4px;
  font-size: 13px;
  color: #495057;
}

.profile-modal__point-info {
  display: flex;
  gap: 0 2px;
}

.profile-modal__point-item {
  padding: 8px;
  border-radius: 2px;
  background-color: #f1f3f5;
}

.profile-modal__point-item.coupon {
  flex: 1;
}

.profile-modal__point-item.point {
  flex: 2;
}

.profile-modal__point-item-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #495057;
  font-size: 14px;
}

.profile-modal__point-item span {
  font-weight: 600;
  font-size: 14px;
  color: #495057;
}

.profile-modal__point-item span.color {
  color: #00c471;
}

.profile-modal__alert-info {
  position: absolute;
  top: -8px;
  right: 12px;
}

.alert-link {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 10px;
}

.alert-link:hover span {
  color: #00c471;
}

.alert-link__icon span {
  font-weight: 300;
  font-size: 22px;
  transition: color 0.2s;
}

.alert-link__red-dot {
  position: absolute;
  top: 10px;
  right: 10px;
}

.profile-modal__menu-list {
  overflow-y: auto;
  scrollbar-width: thin;
  padding: 16px;
  background-color: #f8f9fa;
}

.profile-modal__menu-item:not(:last-child) {
  margin-bottom: 16px;
}

.profile-modal__menu-item-link {
  display: flex;
  align-items: center;
  gap: 0 8px;
  height: 24px;
}

.profile-modal__menu-item-link span {
  /* font-weight: 300; */
  font-size: 15px;
  color: #212529;
}

.profile-modal__menu-item-link span.icon {
  font-size: 20px;
}

.profile-modal__menu-item-link:hover span {
  color: #868e96;
}

.profile-modal__bottom {
  display: flex;
  justify-content: space-between;
  padding: 10px 16px;
  font-size: 14px;
  color: #495057;
}

.btn-logout {
  border: 0;
  background-color: transparent;
}

.btn-logout:hover {
  font-weight: 500;
  color: #212529;
}

.link-cs:hover span {
  font-weight: 500;
  color: #212529;
  text-decoration: underline;
}


/* Header Mobile */
/* .header-mobile .inner {
  display: flex;
  align-items: center;
} */

.header-mobile__left,
.header-mobile__center,
.header-mobile__right {
  display: flex;
  flex: 1;
}

.header-mobile__left {
  justify-content: start;
}

.header-mobile__menu-btn {
  padding-right: 8px;
  border: 0;
  background-color: transparent;
}

.header-mobile__menu-btn span {
  font-size: 30px;
}

.header-mobile__center {
  justify-content: center;
}

.header-mobile__right {
  justify-content: end;
}


/* 모바일 메뉴 모달 */
.mobile-menu-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.mobile-menu-modal__modal {
  position: absolute;
  width: 320px;
  height: 100%;
  background-color: #fff;
  z-index: 200;
}

.mobile-menu-modal__search {
  position: relative;
  height: 70px;
  padding: 12px 16px;
  border-bottom: 1px solid #e9ecef;
}

.mobile-menu-modal__search-input {
  width: 100%;
  height: 100%;
  padding: 8px 36px 8px 8px;
  font-size: 15px;
  color: #5f5f5f;
  border: 1px solid #ced4da;
  border-radius: 8px;
  background: #f1f3f5;
  outline: 0;
  box-sizing: border-box;
}

.mobile-menu-modal__search .header__search-button {
  top: 50%;
  right: 22px;
  translate: 0 -50%;
}

.mobile-menu-modal__menu {
  display: flex;
  height: calc(100% - 70px);
}

.mobile-menu-list {
  flex-shrink: 0;
  width: 150px;
  height: 100%;
  border-right: 1px solid #ced4da
}

.mobile-submenu-list {
  width: 100%;
  overflow-y: auto;
}

.mobile-submenu-list__item{
  display: none;
}
.mobile-submenu-list__item.active{
  display: block;
}

.mobile-menu-list__item {
  display: flex;
  align-items: center;
  padding: 16px;
  font-size: 15px;
  color: #495057;
  line-height: 1;
  border-bottom: 1px solid #e9ecef;
}

.mobile-menu-list__item.drop-down {
  justify-content: space-between;
}

.mobile-menu-list__item.depth1 {
  font-weight: 600;
}

.mobile-menu-list__item.depth2 .icon{
  font-size: 16px;
}

.mobile-menu-list__item.depth3 {
  padding-left: 24px;
  background-color: #f8f9fa;
}

.drop-down-list {
  height: 0;
  overflow: hidden;
  transition: heigth 0.3s;
}

.drop-down-list.active {
  height: auto;
}